:root {
  --cmn-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
    rgb(0 0 0 / 14%) 0px 3px 4px 0px, rgb(0 0 0 / 12%) 0px 1px 8px 0px;
  cursor: default;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

button,
input {
  outline: none;
}
button {
  border: none;
}

.max-width-95 {
  max-width: 95%;
}
.scroll-smooth {
  scroll-behavior: smooth;
}

.custom-sb,
.hide-sb {
  overflow: auto;
}

::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 10px grey;
  border-radius: 8px;
}
::-webkit-scrollbar-thumb {
  background: rgb(165, 165, 165);
  border-radius: 8px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(185, 185, 185);
}

.custom-sb::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.custom-sb::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px grey;
  border-radius: 4px;
}
.custom-sb::-webkit-scrollbar-thumb {
  border-radius: 4px;
}

.hide-sb {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.hide-sb::-webkit-scrollbar {
  display: none; /* Chrome, Safari and Opera */
}

.bg-for-icon {
  background-size: contain;
  background-repeat: no-repeat;
}
.black-arrow-bg {
  background-image: url(https://cdn4.iconfinder.com/data/icons/ionicons/512/icon-arrow-down-b-512.png);
}
.white-arrow-bg {
  background-image: url(https://www.freeiconspng.com/uploads/white-down-arrow-png-2.png);
}

.appearance-none {
  appearance: none;
}
.unavailable {
  opacity: 0.5;
}
.cmn-shadow {
  box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
    rgb(0 0 0 / 14%) 0px 3px 4px 0px, rgb(0 0 0 / 12%) 0px 1px 8px 0px;
}
.white-glow-shadow {
  box-shadow: 0 0 3px white;
}

.glowing {
  -webkit-animation: glow 0.5s ease-in-out infinite alternate;
  -moz-animation: glow 0.5s ease-in-out infinite alternate;
  animation: glow 0.5s ease-in-out infinite alternate;
}
@keyframes glow {
  from {
    box-shadow: 0 0 0 1px #fff, 0 0 1px 1px #fff, 0 0 2px 1px #fff,
      0 0 3px 1px #fff;
  }
  to {
    box-shadow: 0 0 2px 1px #fff, 0 0 3px 2px #fff, 0 0 4px 3px #fff,
      0 0 5px 4px #fff;
  }
}

.contain-info_sign .info_sign {
  color: #ebebeb;
  background-color: transparent;
}
.contain-info_sign:hover .info_sign {
  color: #ffdc4e;
}

@media (min-width: 1025px) {
  .pointer {
    cursor: pointer;
  }
  .glow-on-hover {
    opacity: 0.9;
  }
  .glow-on-hover:hover {
    opacity: 1;
  }
  .zoomin-on-hover:hover {
    transform: scale(1.05);
  }
  .zoomin-on-hover:hover img {
    transform: scale(1.2);
  }
}
